import {
  Alert,
  Button,
  Card,
  Col,
  ConfigProvider,
  DatePicker,
  Divider,
  Form,
  Input,
  List,
  Row,
  Select,
  Space,
  Typography,
} from "antd";
import dayjs from "dayjs";
import { useContext, useEffect, useState } from "react";
import locale from "antd/es/date-picker/locale/es_ES";
import { pagoAsociacionValues } from "../../../data/initalValues";
import MainModal from "../../modal/MainModal";
import { AiOutlineForm } from "react-icons/ai";
import { CrudContext } from "../../../context/CrudContext";
import { notificacion } from "../../../helpers/mensajes";

function ModalAsociacion({
  asociacionTemp,
  open,
  closeModal,
  actualizarTabla,
  closeModalSubmit = () => {},
  dataToEdit = {},
  setDataToEdit = () => {},
}) {
  const [form] = Form.useForm();
  const { Text } = Typography;

  const { updateData, createData } = useContext(CrudContext);

  const [cargando, setCargando] = useState(false);

  // const [asociaciones, setAsociaciones] = useState([]);
  // const [selectAsociacion, setSelectAsociacion] = useState(null);
  const [trabajadores, setTrabajadores] = useState([]);

  // const getAsociaciones = async () => {
  // 	const response = await getData("planilla/pagos/lista");
  // 	if (response) {
  // 		setAsociaciones(response.data);
  // 	}
  // };

  useEffect(() => {
    setTrabajadores(
      asociacionTemp?.pagos.trabajadores.filter((item) => !item.programado)
    );
  }, [asociacionTemp]);

  console.log("====================================");
  console.log(asociacionTemp);
  console.log("====================================");

  // const handleSelectAsociacion = (e) => {
  // 	const data = asociaciones.find((item) => item.contrato_id === e);
  // 	setSelectAsociacion(data);
  // };

  // useEffect(() => {
  // 	getAsociaciones();
  // }, []);

  // useEffect(() => {
  // 	if (dataToEdit) {
  // 		// TODO cambiar por el asociacion -> contrato_id
  // 		setSelectAsociacion(
  // 			asociaciones?.find(
  // 				(item) => item.nombre === dataToEdit.asociacion
  // 			)
  // 		);
  // 		setTrabajadores(dataToEdit.trabajadores);
  // 		setIncentivo(dataToEdit);
  // 	}
  // }, [asociaciones]);

  // const dataAsociaciones = asociaciones?.map((item) => {
  // 	return {
  // 		value: item.contrato_id,
  // 		label: item.nombre,
  // 	};
  // });

  const dataTrabajadores = trabajadores?.map((item) => {
    return {
      value: item.dni,
      label: item.nombre,
    };
  });

  const [seleccionados, setSeleccionados] = useState([]);

  const handleChange = (e) => {
    setSeleccionados(e);
  };

  const [trabajadoresSeleccionados, setTrabajadoresSeleccionados] = useState(
    []
  );

  useEffect(() => {
    if (seleccionados.length > 0) {
      const data = trabajadores.filter((item) =>
        seleccionados.includes(item.dni)
      );
      setTrabajadoresSeleccionados(data);
    }
  }, [seleccionados]);

  const [asociacion, setIncentivo] = useState(pagoAsociacionValues);

  const handleData = (e, text) => {
    if (!text && e.target) {
      const { name, value } = e.target;
      form.setFieldsValue({
        [name]: value,
      });
      setIncentivo((values) => {
        return { ...values, [name]: value };
      });
    } else {
      form.setFieldsValue({
        [text]: e,
      });
      setIncentivo((values) => {
        return { ...values, [text]: e };
      });
    }
  };

  const [trabajadoresSave, setTrabajadoresSave] = useState([]);

  useEffect(() => {
    if (trabajadoresSeleccionados.length > 0) {
      const data = trabajadoresSeleccionados.map((item) => {
        return {
          nombre: item.nombre,
          dni: item.dni,
          teletrans: item.teletrans || "",
        };
      });
      if (dataToEdit) {
        setTrabajadoresSave(
          asociacion.trabajadores.map((item) => {
            return {
              ...item,
              teletrans: parseFloat(item.teletrans),
            };
          })
        );
      } else {
        setTrabajadoresSave(data);
      }
    }
  }, [trabajadoresSeleccionados]);

  const handleDataTrabajador = (e, dni) => {
    const index = trabajadoresSave.findIndex((item) => item.dni === dni);
    trabajadoresSave[index] = {
      ...trabajadoresSave[index],
      teletrans: parseFloat(e.target.value),
    };
    setTrabajadoresSave([...trabajadoresSave]);
  };

  const totalTeletrans = () => {
    let total = 0;
    trabajadoresSave.forEach((item) => {
      total += item.teletrans;
    });
    return total;
  };

  const totalVolquetes = () => {
    if (asociacion.volquetes) return asociacion.volquetes;
    const totalT = totalTeletrans();
    return totalT / 4;
  };

  const handleSubmit = async () => {
    const route = "pago/programacion/multiple";

    const totalVolquetesConvertido = totalVolquetes() * 4;

    // Verifica si el total de volquetes convertido a teletrans es mayor que totalModificado
    if (totalVolquetesConvertido > asociacionTemp.total_modificado) {
      // Si es mayor, muestra un mensaje de error y detiene la ejecución
      notificacion(500, "El monto a pagar es mayor a lo adeudado.");
      return;
    }
    // setCargando(true);
    const data = {
      observacion: asociacion.observacion,
      contrato_id: asociacionTemp.contrato_id,
      fecha_pago: dayjs(asociacion.fecha_pago).format("YYYY-MM-DD") || "",
      volquetes: totalVolquetes(),
      teletrans: totalTeletrans(),
      tipo: "asociacion",
      quincena: asociacionTemp.quincena,
      asociacion: trabajadoresSave.map((item) => {
        return {
          volquetes: totalVolquetes(),
          trabajador_dni: item.dni,
          teletrans: item.teletrans || 0,
          contrato_id: asociacionTemp.contrato_id || "",
          quincena: asociacionTemp.quincena || "",
        };
      }),
    };
    const dataUpdate = {
      observacion: asociacion.observacion,
      contrato_id: asociacionTemp.contrato_id,
      fecha_pago: dayjs(asociacion.fecha_pago).format("YYYY-MM-DD") || "",
      volquetes: totalVolquetes(),
      teletrans: totalTeletrans(),
      trabajadores: trabajadoresSave.map((item) => {
        return {
          volquetes: totalVolquetes(),
          trabajador_dni: item.dni,
          teletrans: item.teletrans,
          contrato_id: asociacionTemp.contrato_id || "",
          quincena: asociacionTemp.quincena || "",
        };
      }),
    };
    if (dataToEdit) {
      const response = await updateData(
        dataUpdate,
        dataToEdit.id,
        "planilla/asociacion"
      );
      if (response) {
        notificacion(response.status, response.msg);
        actualizarTabla();
        handleClose();
      }
    } else {
      const response = await createData(data, route);
      if (response) {
        notificacion(response.status, response.msg);
        actualizarTabla();
        handleClose();
      }
    }
  };

  const [
    nombresTrabajadoresSeleccionados,
    setNombresTrabajadoresSeleccionados,
  ] = useState([]);

  useEffect(() => {
    if (dataToEdit) {
      setIncentivo({
        ...dataToEdit,
        fecha_pago: dayjs(dataToEdit.fecha_pago).format("YYYY-MM-DD"),
      });
      setSeleccionados(dataToEdit.trabajadores.map((item) => item.dni));
      setNombresTrabajadoresSeleccionados(
        dataToEdit.trabajadores.map((item) => item.nombre)
      );
      setTrabajadoresSave(dataToEdit.trabajadores);
    }
  }, [dataToEdit]);

  const handleClose = () => {
    setDataToEdit(null);
    closeModal();
    closeModalSubmit();
    setSeleccionados([]);
    setTrabajadores([]);
    setTrabajadoresSeleccionados([]);
    setTrabajadoresSave([]);
    setIncentivo(pagoAsociacionValues);
    // setSelectAsociacion(null);
  };

  return (
    <MainModal
      open={open}
      closeModal={handleClose}
      title={"Registrar pago Asociación: " + asociacionTemp?.nombre}
      width={800}
    >
      <Form
        form={form}
        className="modal-body"
        onFinish={handleSubmit}
        layout="vertical"
      >
        {/* <Form.Item label="Seleccione asociación">
					<Select
						placeholder="Seleccione asociación"
						style={{ width: "100%" }}
						allowClear
						value={asociacionTemp?.nombre}
						onChange={handleSelectAsociacion}
						options={dataAsociaciones}
						disabled={dataToEdit ? true : false}
					/>
				</Form.Item> */}
        <Form.Item label="Observación">
          <Select
          autoSize
            name="observacion"
            dropdownMatchSelectWidth={false}
            onChange={(event) => handleData(event, "observacion")}
            options={[
              {
                label: "ANA MARÍAS",
                value: "ANA MARÍAS",
              },
              {
                label: "SANTA MARIA",
                value: "SANTA MARIA",
              },
              {
                label: "CORAZÓN DE SAN FRANCISCO",
                value: "CORAZÓN DE SAN FRANCISCO",
              },
              {
                label: "SAPO DE ORO",
                value: "SAPO DE ORO",
              },
            ]}
          />
        </Form.Item>
        <Form.Item label="Fecha">
          <ConfigProvider locale={locale}>
            <DatePicker
              allowClear={false}
              value={dayjs(asociacion.fecha_pago)}
              name="fecha_pago"
              placeholder="Fecha de Pago"
              picker="day"
              onChange={(e) => handleData(e, "fecha_pago")}
              style={{
                width: "100%",
              }}
              format={"YYYY-MM-DD"}
            />
          </ConfigProvider>
        </Form.Item>
        <Divider orientation="left">Trabajadores</Divider>
        <Space direction="vertical" style={{ width: "100%" }}>
          <Form.Item label="Seleccione trabajadores">
            <Select
              showSearch
              mode="multiple"
              placeholder="Seleccione trabajadores"
              style={{ width: "100%" }}
              allowClear
              value={seleccionados}
              onChange={handleChange}
              options={dataTrabajadores}
              disabled={dataToEdit ? true : false}
              filterOption={(input, option) =>
                option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            />
          </Form.Item>
          <Form.Item label="Volquetes">
            <Input
              value={asociacion.volquetes}
              placeholder="Volquetes"
              name="volquetes"
              type="number"
              min={0}
              onChange={(e) => handleData(e)}
            />
          </Form.Item>
        </Space>
        <Divider orientation="left">Teletrans por trabajador</Divider>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            gap: 4,
          }}
        >
          <Card style={{ width: "100%" }}>
            <List
              itemLayout="horizontal"
              dataSource={trabajadoresSave}
              renderItem={(item) => (
                <List.Item>
                  <Row key={item.dni} style={{ width: "100%" }}>
                    <Col span={18}>
                      <Space direction="vertical">{item.nombre}</Space>
                    </Col>
                    <Col span={6}>
                      <Space direction="vertical">
                        <Input
                          value={item.teletrans}
                          placeholder="Teletrans"
                          type="number"
                          min={0}
                          onChange={(e) => handleDataTrabajador(e, item.dni)}
                        />
                      </Space>
                    </Col>
                  </Row>
                </List.Item>
              )}
            />
            <Space
              direction="horizontal"
              style={{
                width: "100%",
                justifyContent: "end",
                alignItems: "center",
                padding: 24,
              }}
            >
              <Text type={totalTeletrans() % 4 === 0 ? "success" : "danger"}>
                TOTAL DE TELETRANS
              </Text>
              <Alert
                message={totalTeletrans()}
                type={totalTeletrans() % 4 === 0 ? "success" : "error"}
              />
            </Space>
          </Card>
          {totalTeletrans() > 4 && (
            <Alert
              message="Total de pago no debe exceder a 4 teletrans"
              type="error"
            />
          )}
        </div>
        <Space
          direction="horizontal"
          style={{
            width: "100%",
            marginTop: 20,
            justifyContent: "end",
          }}
        >
          <Button onClick={closeModal}>Cancelar</Button>
          <Button
            type="primary"
            htmlType="submit"
            icon={<AiOutlineForm />}
            loading={cargando ? true : false}
          >
            {/* {dataToEdit ? "Editar" : "Registrar"} */}
            Registrar
          </Button>
        </Space>
      </Form>
    </MainModal>
  );
}
export default ModalAsociacion;
