import { Table, Tag } from "antd";
import React from "react";
import DataTable from "react-data-table-component";

import "./tabla.css";

const TablaAsociacion = ({ columns, table, filas }) => {
	const columnsGeneral = columns.filter(
		(item) =>
			item.id === "Nro" ||
			item.id === "asociacion" ||
			item.id === "fecha_inicio" ||
			item.id === "fecha_fin" ||
			item.id === "pago" ||
			item.id === "saldo" ||
			item.id === "tipo_asociacion" ||
			item.id === "observacion" ||
			item.id === "acciones"
	);

	const columnsExpandable = columns.map((item) => {
		return {
			title: item.name,
			dataIndex: item.id,
			key: item.id,
			// sorter: (a, b) => {
			// 	const valA = a[item.id];
			// 	const valB = b[item.id];
			// 	if (typeof valA === "boolean" && typeof valB === "boolean") {
			// 		return valA === valB ? 0 : valA ? 1 : -1;
			// 	} else if (isNaN(valA) || isNaN(valB)) {
			// 		return valA.localeCompare(valB);
			// 	} else {
			// 		return valA - valB;
			// 	}
			// },
		};
	});

	const paginationComponentOptions = {
		rowsPerPageText: "Filas por página",
		rangeSeparatorText: "de",
		selectAllRowsItem: false,
		selectAllRowsItemText: "Todos",
	};

	const ExpandedComponent = ({ data }) => (
		<Table
			size="small"
			columns={columnsExpandable.filter(
				(item) =>
					item.key === "Nro" ||
					item.key === "nombre" ||
					item.key === "dni" ||
					item.key === "telefono" ||
					item.key === "teletrans" ||
					item.key === "estado"
			)}
			pagination={false}
			dataSource={data.pagos?.trabajadores?.map((item, index) => {
				return {
					key: index,
					Nro: index + 1,
					nombres: item.nombre,
					dni: item.dni,
					telefono: item.telefono,
					teletrans: item.teletrans || 0,
					estado: item.programado ? (
						<Tag color="green">Programado</Tag>
					) : (
						<Tag color="red">No programado</Tag>
					),
					...item,
				};
			})}
		/>
	);

	return (
		<div className="table-container">
			<DataTable
				size="small"
				columns={columnsGeneral}
				data={table}
				pagination
				striped
				highlightOnHover
				responsive
				paginationComponentOptions={paginationComponentOptions}
				paginationPerPage={filas || 8}
				paginationRowsPerPageOptions={[8, 16, 24, 32, 40]}
				expandableRows
				expandableRowsComponent={ExpandedComponent}
			/>
		</div>
	);
};

export default TablaAsociacion;
