import { Col, Empty, Row } from "antd";
import React, { useContext, useEffect, useState } from "react";
import { AiOutlineForm } from "react-icons/ai";
import { CrudContext } from "../../../context/CrudContext";
import {
	asociacionLayout,
	AsociacionLayout,
	pagoAsociacionLayout,
} from "../../../data/dataTable";
import { notificacion } from "../../../helpers/mensajes";
import ButtonAdd from "../../Button/ButtonAdd";
import Cargando from "../../cargando/Cargando";
import Header from "../../header/Header";
import TablaAsociacion from "../../tabla/TablaAsociacion";
import ObservacionAprobacion from "../aprobaciones/ObservacionAprobacion";
import ModalAsociacion from "./ModalAsociacion";

const Asociacion = () => {
	const { getData, dataToEdit, setDataToEdit, deleteData } =
		useContext(CrudContext);

	const [cargando, setCargando] = useState(false);
	const [modal, setModal] = useState(false);
	const [asociacionTemp, setAsociacionTemp] = useState({});
	const [observaciones, setObservaciones]=useState(false);
	const [dataObservacion, setDataObservacion] = useState("")

	const handleOpenModal = (asociacion) => {
		setModal(true);
		setAsociacionTemp(asociacion);
	};

	const [asociacion, setAsociacion] = useState([]);

	const getAsociacion = async () => {
		const response = await getData("planilla/pagos/lista");
		if (response) {
			const filterNull = response.data.filter((item) => item !== null);
			setAsociacion(filterNull);
			setCargando(false);
		}
	};
	useEffect(() => {
		getAsociacion();
	}, []);

	const closeModal = () => {
		setModal(false);
		setDataToEdit(null);
	};

	const handleObservaciones = (e) =>{

		setDataObservacion(e?.aprobacion)
		setObservaciones(true)
	}

	// TODO Editar y eliminar
	// const handleEdit = (e) => {
	// 	setModal(true);
	// 	setDataToEdit(e);
	// };

	// const handleDelete = async (e) => {
	// 	const response = await deleteData("pago/asociacion", e);
	// 	if (response) {
	// 		notificacion(response.status, response.msg);
	// 		getAsociacion();
	// 	}
	// };

	// const columns = pagoAsociacionLayout(handleEdit, handleDelete);
	const columns = pagoAsociacionLayout(handleOpenModal, handleObservaciones);

	return (
		<>
			<Header text={"Asociacion"} ruta={"/planilla"} />
			<div className="margenes">
				{/* <Row align="middle">
          <Col span={18}></Col>
          <Col span={6} align="end">
            <ButtonAdd
              title="Crear pago asociacion"
              onClick={() => setModal(true)}
              icon={<AiOutlineForm />}
            />
          </Col>
        </Row> */}
				{asociacion?.length > 0 ? (
					<TablaAsociacion columns={columns} table={asociacion} />
				) : (
					<>
						{cargando ? (
							<Cargando />
						) : (
							<Empty
								image={Empty.PRESENTED_IMAGE_SIMPLE}
								description={
									<span>No hay registros para mostrar.</span>
								}
							/>
						)}
					</>
				)}
			</div>
			{modal && (
				<ModalAsociacion
					asociacionTemp={asociacionTemp}
					actualizarTabla={getAsociacion}
					open={modal}
					closeModal={closeModal}
					dataToEdit={dataToEdit}
				/>
			)}
			{observaciones &&
				 <ObservacionAprobacion
				 setModal={setObservaciones}
				 modal={observaciones}
				 data={dataObservacion}
				 observacion={""}
				 boton = {true}
			   />
			}
		</>
	);
};

export default Asociacion;
